.main-container {
  max-width: 1000px;
  margin: 0 auto;
}

.grid-group {
  display: grid; 
  grid-template-columns: 1fr 1fr 1fr 2fr;
  gap: 0px;
  height: 100%;
}

.grid-group-2 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 0px;
  height: 100%;
}

.about-me {
  grid-row-start: 1;
  grid-column-start: 1;
  grid-row-end: 2;
  grid-column-end: 4;
}

.contacts {
  grid-row-start: 1;
  grid-column-start: 4;
  grid-row-end: 2;
  grid-column-end: 6;
}

.skills { 
  grid-row-start: 2;
  grid-column-start: 1;
  grid-row-end: 3;
  grid-column-end: 6;
}

.projects {
  grid-row-start: 3;
  grid-column-start: 1;
  grid-row-end: 6;
  grid-column-end: 3;
}

.experience {
  grid-row-start: 3;
  grid-column-start: 3;
  grid-row-end: 6;
  grid-column-end: 6;
}

@media (min-width: 320px) and (max-width: 500px) {
  .main-container {
    max-width: 1000px;
    margin: 0 auto;
  }

  .App {
    max-width: 525px;
  }

  .grid-group-2 {
    display: contents; 
  }
}
