.profile {
    &__content {
        &-avatar{
            width: 150px;
            height: 150px;
            border-radius: 50%;
            position: absolute;
            margin-top: -75px;
            margin-left: 30px;

            .avatar {
                width: 150px;
                height: 150px;
                border-radius: 50%;
                border: 5px solid #222222;
            }
        }
    }

    @media (min-width: 320px) and (max-width: 500px) {
        .profile {
            &__content {
                height: 225px;
                    &-avatar{
                    position: relative;
                    margin: 0;
                    padding: 0;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    
                    .avatar{
                        margin-top: -75px;
                    }
                }
            }
        }
    }
}