.about-me {
    color: #E1E3E6;
    background-color: #222222;
    margin-top: 16px;
    border: 0.5px solid #424242;
    border-radius: 20px;
    height: 200px;
    margin-right: 16px;
 
    span {
        font-size: 18px;
        position: relative;
        top: 10px;
        margin: 20px 0 0 20px;
        font-weight: bold;
    }

    &__text {
        margin: 20px;
        font-size: clamp(11px, 1.63vw, 17px)
    }
    
    @media (min-width: 320px) and (max-width: 500px) {
        display: none;
    }
}