.profile {
    color: #E1E3E6;
    margin-top: 66px;
    border: 0.5px solid #424242;
    border-radius: 20px;

    &__content {
        background-color: #222222;
        width: 100%;
        height: 100px;
        margin-top: -25px;
        border-radius: 20px;

        &-info {
            position: relative;
            top: 20px;
            margin-left: 200px;
            font-size: clamp(11px, 1.63vw, 17px);

            .name-info {
                margin-top: 30px;
                color: #E1E3E6;
                font-size: 20px;
                font-weight: 600;
                letter-spacing: 1px;
            }

            .other-info {
                display: flex;
                margin-right: 10px;
                margin-left: -7px;


                .location, .education, .moreDetailed, .cake {
                    margin-top: 10px;
                    margin-right: 10px;
                    display: flex;
                    align-items: center;
                }

                .education
                    svg {
                        margin-right: 5px;
                    }
                }
                .moreDetailed {
                    cursor: pointer;
                    display: block;
                    svg {
                        margin-right: 3px;
                    }
                }

                .mobileProfile {
                    display: none;
                }

            }
        }

@media (min-width: 320px) and (max-width: 500px) {
    
        margin-top: 45px;

        &__content {
            &-info {
                top: -90px;
                margin-left: 0;
                display: flex;
                flex-direction: column;
                align-items: center;

                .name-info {
                    margin-top: 30px;
                    color: #E1E3E6;
                    font-size: 20px;
                    font-weight: 600;
                    letter-spacing: 1px;
                }

                .other-info {
                    display: flex;
                    margin-right: 0;
                    margin-left: 0;
                    font-size: 16px;
                    margin-top: -13px;

                    .location, .education, .moreDetailed {
                        margin-right: 5px;
                    }
                }

                .education svg {
                    margin-right: 5px;
                }

                .moreDetailed {
                    span {
                        display: none
                    } 
                    svg {
                        margin-right: 3px;
                    }                            
                }

                .mobileProfile {
                    display: contents;

                    &__button{
                        margin: 10px;
                    }
                }  
            }
        }
    }
}
    