.modal {
    height: 100vh;
    width: 100vh;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &__overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 0;
    }
}

.modal__active {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    z-index: 2;
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: #333333;
    height: 400px;
    width: 400px;
    transition: 0.5s;
    z-index: 3;

    &-header-title {
        margin-top: 0;
    }
    
    &-title {
        display: flex;
        justify-content: space-between;
    }

    &-about-me {
       p {
        padding-left: 5px;
        font-weight: bold;
       }

       span {
        margin-left: 5px;
        color: #939393;
       }

       .location, .cake, .education {
        margin-top: 0 !important;
       }
    }

    svg {
        cursor: pointer;
    }

    @media (min-width: 320px) and (max-width: 500px) {
        
    }
   
}