.skills {
    color: #E1E3E6;
    background-color: #222222;
    margin-top: 16px;
    border: 0.5px solid #424242;
    border-radius: 20px;
    height: 200px;

    span {
        font-size: 18px;
        position: relative;
        top: 10px;
        margin: 20px 0 0 20px;
        font-weight: bold;
    }

    .container {
        display: flex;
        justify-content: center;
        grid-template-columns: repeat(9, 1fr);
        grid-gap: 1rem;
        align-items: center;
        margin-top: 20px;
        justify-items: center;
        
        img {
            filter: drop-shadow(3px 5px 2px #000);
        }
    }
    
    &__item {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 10px 10px;
        width: 60px;
        margin: 5px 0;
        border-radius: 8px;
        transition: 0.5s;

        &:hover {
            background-color: rgba(248,245,244,.05);
            transform: scale(1.4);
            transition: 0.5s;
        }

        &-el {
            display: flex;
            flex-direction: column;
            align-items: center;

            h4 {
                filter: drop-shadow(3px 5px 2px #000);
                font-size: 14px;
                text-align: center;
                letter-spacing: 0.5px;
            }
        }

        &-img {
            width: 50px ;
            height: 50px;
        }

        &-title {
            font-size: 14px;
            color: #656565;
            letter-spacing: 0.5px;
            text-align: center;
            margin-bottom: 0;

        }
    }

    @media (min-width: 320px) and (max-width: 500px) {

        height: 300px;

        &__item {
            padding: 0;

            &:hover {
                background-color: #222222;
                transform: none;
                transition: 0;
            }
        }

        .container {
            display: flex;
            justify-content: center;
            align-items: center;
    
            display: grid;
            grid-template-columns: repeat(5, minmax(50px, auto));
            grid-template-rows: none;
            align-items: center;
            margin-top: 5rem;
            margin-top: 20px;
            justify-items: center;
        }
    }
}